 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-27 16:36:02
 * @LastEditTime: 2019-08-30 17:45:54
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="financial_wrapper">
    <Header></Header>

    <div class="financial_content">
        <div class="cru_content">
            <bread-crumb :crumbs="crumbs"></bread-crumb>
        </div>
        <el-row class="financial_container">
            <el-col>
                <el-table :data="finans"  ref="table" highlight-current-row>
                    <el-table-column type="expand">
                        <template slot-scope="scope">
                            <el-row class="particulars">
                                <el-col class="com_col" :span="12">
                                    <p class="com_title fl">{{scope.row.type == 0?'汇出时间：':scope.row.type ==1?'汇入时间：':''}}</p>
                                    <p class="com_content fl">{{scope.row.detail.t | fullDate}}</p>
                                </el-col>
                                <el-col class="com_col" :span="12">
                                    <p class="com_title fl">手续费：</p>
                                    <p class="com_content fl">{{scope.row.detail.fee}}</p>
                                </el-col>
                                <el-col class="com_col" :span="12">
                                    <p class="com_title fl">{{scope.row.type == 0?'充币地址：':scope.row.type ==1?'提币地址：':''}}</p>
                                    <p class="com_content fl">{{scope.row.detail.address}}</p>
                                </el-col>
                                <el-col class="com_col" :span="12">
                                    <p class="com_title fl">交易ID：</p>
                                    <p class="com_content fl">{{scope.row.detail.tx || '无'}}</p>
                                    <p class="copy fl" v-clipboard:success="onCopy" v-clipboard:copy="scope.row.detail.tx">复制</p>
                                </el-col>
                                <el-col class="com_col" :span="12">
                                    <p class="com_title fl">链类型：</p>
                                    <p class="com_content fl">{{scope.row.detail.type}}</p>
                                </el-col>
                            </el-row>
                        </template>
                    </el-table-column>

                    <el-table-column align="left" :show-overflow-tooltip="true" prop="create_time" label="时间" >
                        <template slot-scope="scope">
                            {{scope.row.create_time | fullDate}}
                        </template>
                    </el-table-column>
                    <el-table-column align="left" :show-overflow-tooltip="true" prop="coin" label="币种" width="150"> </el-table-column>
                    <el-table-column align="left" :show-overflow-tooltip="true" label="类型" >
                        <template slot-scope="scope">
                            {{scope.row.type == 0?'普通提币':scope.row.type == 1?'充值转入':scope.row.type == 2?'划转转入':scope.row.type == 3?'划转转出':scope.row.type == 4?'活动奖励':''}}
                        </template>
                    </el-table-column>
                    <el-table-column align="left" :show-overflow-tooltip="true" prop="total" label="数量" ></el-table-column>
                    <el-table-column align="left" :show-overflow-tooltip="true" label="状态" >
                        <template slot-scope="scope">
                            {{scope.row.status == 0?'未完成':scope.row.status == 1?'充值中':scope.row.status == 2?'提币中':scope.row.status == 3?'已完成':''}}
                        </template>
                    </el-table-column>
                    <el-table-column align="left" :show-overflow-tooltip="true"  label="操作">
                        <template slot-scope="scope">
                            <span class="opera" v-if="scope.row.type ==0 || scope.row.type ==1" @click="toogleExpand(scope.row)">详情</span>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
    </div>
  </div>
</template>

<script>
    import Header from '@/components/header/header'
    import BreadCrumb from '@/components/breadcrumb/breadcrumb'
    import Mock from '@/utils/mock'

    export default {
        components: {Header,BreadCrumb},
        props: {},
        data() {
            return {
                crumbs:[],
                finans:[]
            }
        },
        watch: {},
        computed: {},
        methods: {
            /* 解析路由地址 */
            analyAddress(){
                const route = this.$route
                const bread = JSON.parse(localStorage.getItem('breads'))
                var cbs = [{name:'财务记录',url:this.$route.fullPath}]
                cbs.unshift(bread)
                this.crumbs = cbs
            },
            /* 表格手风琴效果 */
            toogleExpand(row) {
                // console.log('row', row)
                let $table = this.$refs.table;
                this.finans.map((item) => {
                    if (row.id != item.id) {
                        $table.toggleRowExpansion(item, false)
                    }else{
                        item.expansion = !item.expansion
                    }
                })
                $table.toggleRowExpansion(row)
            },

            /* 财务记录获取 */
            getFinacials(){
                var data = {"limit":''}
                this.$store.dispatch('financial_record', data).then(res => {
                    // console.log('res', res)
                    if(res.data.result == 0){
                        // this.finans = res.data.data || []
                        for(var i = 0;i<res.data.data.length;i++){
                            res.data.data[i].id = i
                        }
                        this.finans = res.data.data || []
                        // console.log(this.finans)
                    }
                }).catch(err => {

                })
            },

            /* 复制 */
            onCopy (e) {
                this.$message.success('成功复制到剪贴板')
            }
        },
        created() {
            this.analyAddress()
            this.getFinacials()
        },
        mounted() {}
    };
</script>
<style lang="scss" scoped>
    @import '@/style/base.scss';

    .financial_wrapper{
        width: 100%;
        min-height: 100%;
        background-color: $other_bg;
        min-width: 1280px;
        padding-top: 45px;
        padding-bottom: 15px;
        box-sizing: border-box;
        .financial_content{
            width: 1200px;
            margin: 0 auto;
            .cru_content{
                padding: 15px 0;
                box-sizing: border-box;
            }
            .financial_container{
                width: 100%;
                background-color: $light_font_color;
                padding: 0 20px 40px;
                .opera{
                    color: $active_font_color;
                    font-size: $base_font;
                    margin-right: 10px;
                    cursor: pointer;
                }
                .particulars{
                    width: 100%;
                    border: 1px solid $border_dark_color;
                    padding: 0 0px 0px;
                    box-sizing: border-box;
                    .com_col{
                        line-height: 50px;
                        border-bottom: 1px solid $border_dark_color;
                        padding: 0 15px;
                        box-sizing: border-box;
                        .com_title{
                            color: $dark_title_color;
                            font-size: $base_font;
                        }
                        .com_content{
                            color: $noraml_font_color;
                            font-size: $base_font;
                        }
                        .copy{
                            color: $active_font_color;
                            font-size: $base_font;
                            margin-left: 30px;
                            cursor: pointer;
                        }
                        &:last-child{
                            border-bottom: 0;
                        }
                    }
                }
            }
            
        }
    }
</style>


